import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

//? react component used to create sweet alerts
//import SweetAlert from "react-bootstrap-sweetalert";

//? @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Divider from "@material-ui/core/Divider";
import Checkbox from "@material-ui/core/Checkbox";
import Icon from "@material-ui/core/Icon";

//? @material-ui/icons
import Email from "@material-ui/icons/Email";
// import LockOutline from "@material-ui/icons/LockOutline";

//? core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import CardFooter from "components/Card/CardFooter.js";
import LoadingSpinner from "components/Loading/LoadingSpinner";

import FormControlLabel from "@material-ui/core/FormControlLabel";

//? Authentication Actions
//import { authActions } from "Vintro/store/Auth/auth";
import { loginInWithCredentials } from "Vintro/store/Auth/auth-methods";

//? APIKit to use Axios
//import APIKit, { setClientToken } from "Vintro/API/APIKit";

import styles from "assets/jss/Vintro/views/loginPageStyle.js";

//? Images
import vintroLogoLogin from "assets/img/logo/Vintro_Logo_Gradient.png";
import loginPageImg1 from "assets/img/login/LoginPageImg1.png";

const useStyles = makeStyles(styles);

// * function that returns true if value is email, false otherwise
const verifyEmail = (value) => {
  var emailRex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  if (emailRex.test(value)) {
    return true;
  }
  return false;
};
// * function that verifies if a string has a given length or not
const verifyLength = (value, length) => {
  if (value.length >= length) {
    return true;
  }
  return false;
};

export default function LoginPage() {
  // login form
  const [loginEmail, setloginEmail] = useState("");
  const [loginEmailState, setloginEmailState] = React.useState("");
  const [loginPassword, setloginPassword] = React.useState("");
  const [loginPasswordState, setloginPasswordState] = React.useState("");
  const [rememberUser, setRememberUser] = React.useState(false);
  const [formIsValid, setFormIsValid] = React.useState(false);
  const dispatch = useDispatch();
  const loginStore = useSelector((state) => state.auth);

  const { loading: isLoading, error } = loginStore;

  useEffect(() => {
    if (loginEmailState === "success" && loginPasswordState === "success") {
      setFormIsValid(true);
    } else {
      setFormIsValid(false);
    }
  }, [loginEmailState, loginPasswordState]);

  const formSubmitHandler = (event) => {
    event.preventDefault();
    dispatch(loginInWithCredentials(loginEmail, loginPassword));
  };

  const classes = useStyles();

  return (
    <>
      {isLoading && (
        <div className={classes.loading}>
          <LoadingSpinner />
        </div>
      )}
      {/* {alert} */}
      <div className={classes.container}>
        <Card login>
          <GridContainer justify="center">
            <GridItem xs={12} sm={10} md={7}>
              <img style={{ width: "80%" }} src={loginPageImg1} alt="..." />
              <div className={classes.mainHeader}>
                <h1>
                  <strong>
                    Maximise Your <br />
                    Campus Placements.
                  </strong>
                </h1>
              </div>
            </GridItem>
            <GridItem xs={12} sm={6} md={5} className={classes.loginMain}>
              <CardHeader
                className={`${classes.cardHeader} ${classes.textCenter}`}
              >
                <img
                  className={classes.vintroLogo}
                  //style={{ width: "30%" }}
                  src={vintroLogoLogin}
                  alt="..."
                />
              </CardHeader>
              <Divider />
              <CardBody>
                <form className={classes.loginSub}>
                  <CustomInput
                    success={loginEmailState === "success"}
                    error={loginEmailState === "error"}
                    labelText="Email..."
                    id="email"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <Email className={classes.inputAdornmentIcon} />
                        </InputAdornment>
                      ),
                      onChange: (event) => {
                        if (verifyEmail(event.target.value)) {
                          //currently username is used and not email
                          //if (verifyLength(event.target.value, 5)) {
                          setloginEmailState("success");
                        } else {
                          setloginEmailState("error");
                        }
                        setloginEmail(event.target.value);
                      },
                    }}
                  />
                  <CustomInput
                    success={loginPasswordState === "success"}
                    error={loginPasswordState === "error"}
                    labelText="Password"
                    id="password"
                    formControlProps={{
                      fullWidth: true,
                    }}
                    inputProps={{
                      endAdornment: (
                        <InputAdornment position="end">
                          <Icon className={classes.inputAdornmentIcon}>
                            lock_outline
                          </Icon>
                        </InputAdornment>
                      ),
                      onChange: (event) => {
                        if (verifyLength(event.target.value, 5)) {
                          setloginPasswordState("success");
                        } else {
                          setloginPasswordState("error");
                        }
                        setloginPassword(event.target.value);
                      },
                      type: "password",
                      autoComplete: "off",
                    }}
                  />
                  <h5 className={classes.loginError}>{error}</h5>
                  <GridContainer>
                    <GridItem md={6}>
                      <FormControlLabel
                        control={
                          <Checkbox
                            tabIndex={-1}
                            value={rememberUser}
                            onClick={(event) =>
                              setRememberUser(event.target.checked)
                            }
                            classes={{
                              checked: classes.checked,
                              root: classes.checkRoot,
                            }}
                          />
                        }
                        classes={{
                          label: classes.label,
                          root: classes.labelRoot,
                        }}
                        label="Remember me"
                      />
                    </GridItem>
                    <GridItem md={6}>
                      <div className={classes.forgotPassword}>
                        <a
                          href="#"
                          color="info"
                          className={classes.justifyContentCenter}
                        >
                          Forgot Password?
                        </a>
                      </div>
                    </GridItem>
                  </GridContainer>
                </form>
              </CardBody>
              <CardFooter className={classes.justifyContentCenter}>
                <Button
                  color="info"
                  onClick={formSubmitHandler}
                  disabled={!formIsValid}
                >
                  Sign In
                </Button>
              </CardFooter>
            </GridItem>
          </GridContainer>
        </Card>
      </div>
    </>
  );
}
