import React from "react"
import avatar from '../../../assets/img/default-avatar.gif';
import mic from '../../../assets/img/mic.gif';
import playing from '../../../assets/img/playing.gif';
const getQuestionsService = [
    "What is a first class function in Javascript?",
    "What is Node.js and how it works?",
    "How do you manage packages in your node.js project?",
    "How is Node.js better than other frameworks most popularly used?",
    "Explain the steps how Control Flow controls the functions calls?"
]
var uploadAnswerService = []
const INTERVIEW_STATES = {
    ASKING_QUESTION:'asking',
    COMPLETE_QUESTION: 'compleate',
    COMPLETE_INTERVIEW: 'completeInterview'

}
var mediaRecorder;
class AttendInterviewPage extends React.Component {
  constructor(props) {
        super(props);
        this.state = {currentQuestionIndex : 0 , interviewState:INTERVIEW_STATES.ASKING_QUESTION, counter:0};
  }
  componentDidMount = () =>{
      this.accessMic();
      setInterval(() => {
          const {counter} = this.state;
          this.setState({counter:counter+1});
      }, 1000);
   
  }
  askQuestionInVoice = (question) =>{
    let utter = new window.SpeechSynthesisUtterance();
    utter.lang = 'en-IN';
    utter.text = question;
    var voices = window.speechSynthesis.getVoices();
    utter.voice = voices[10]; 
    
    utter.onstart = () => {
        const { interviewState} = this.state;
        if(interviewState !== INTERVIEW_STATES.COMPLETE_INTERVIEW){
            this.setState({interviewState:INTERVIEW_STATES.ASKING_QUESTION})
        }
    }
    utter.onend = () => {
        const { currentQuestionIndex, interviewState} = this.state;
        if(interviewState === INTERVIEW_STATES.COMPLETE_INTERVIEW){
             if(uploadAnswerService[currentQuestionIndex]){
                uploadAnswerService[currentQuestionIndex].play();
             }
        }else{
            this.setState({interviewState:INTERVIEW_STATES.COMPLETE_QUESTION})
            this.startRecord();
        }
        this.setState({counter:0});
        
    }
    window.speechSynthesis.speak(utter);
  }
  onNextQuestion = () =>{
    
    if(!this.processing){
        const { interviewState, currentQuestionIndex } = this.state;
        if(interviewState !== INTERVIEW_STATES.COMPLETE_INTERVIEW){
            mediaRecorder.stop();
        }
        const nextQuestionIndex =  currentQuestionIndex + 1;
        if(getQuestionsService.length > nextQuestionIndex){
            this.setState({currentQuestionIndex:nextQuestionIndex});
            this.askQuestionInVoice(getQuestionsService[nextQuestionIndex]);
        }else{
            if(interviewState !== INTERVIEW_STATES.COMPLETE_INTERVIEW){
                this.onInterviewFinish()
            }else{
                alert('PLAYBACK COMPLETED SUCCESSFULLY')
                window.location.href = '/';
            }
        }
        
    }
    this.processing = true;
    setTimeout(() => {
        this.processing = false;
    }, 2000);
  }
  onInterviewFinish = () =>{
    this.setState({currentQuestionIndex:0,interviewState:INTERVIEW_STATES.COMPLETE_INTERVIEW})
    this.askQuestionInVoice(getQuestionsService[0]);
  }
  accessMic = () =>{
    navigator.mediaDevices.getUserMedia({ audio: true })
    .then(stream => {
        window.stream = stream;
        const { currentQuestionIndex } = this.state;
        this.askQuestionInVoice(getQuestionsService[currentQuestionIndex]);
    });
  }
  startRecord = () => {
        if(mediaRecorder){
            mediaRecorder.removeEventListener("dataavailable", event => {
                console.log(event)
            });
            mediaRecorder.removeEventListener("stop", event => {
                console.log(event)
            });
            mediaRecorder = null;
        }
        window.options = { mimeType: 'audio/webm' };
        mediaRecorder = new MediaRecorder(window.stream);
        mediaRecorder.start();
        const audioChunks = [];
        mediaRecorder.addEventListener("dataavailable", event => {
            console.log('data avaialble')
            audioChunks.push(event.data);
        });

        mediaRecorder.addEventListener("stop", () => {
            const audioBlob = new Blob(audioChunks);
            const audioUrl = URL.createObjectURL(audioBlob);
            const audio = new Audio(audioUrl);
            uploadAnswerService.push(audio)
            //audio.play();
        });
   }

   /**
    * Playback section
    */

  render(){
    const { currentQuestionIndex , interviewState, counter} = this.state;
    return (
        <section>
            <h2>{interviewState === INTERVIEW_STATES.COMPLETE_INTERVIEW ? "PLAYING RECORDED INTERVIEW":"RECORDING INTERVIEW ANSWERS"}</h2>
        <div>
            <h4>{getQuestionsService[currentQuestionIndex]}</h4>
            <img height="150" src={interviewState === INTERVIEW_STATES.COMPLETE_INTERVIEW ? playing:(interviewState === INTERVIEW_STATES.ASKING_QUESTION ?  avatar : mic)} alt="Creative Tim Logo" />
            <br/> <br/>
            <button className={(interviewState === INTERVIEW_STATES.ASKING_QUESTION || counter < 8 ) &&   interviewState !== INTERVIEW_STATES.COMPLETE_INTERVIEW ? "hide":""} onClick={this.onNextQuestion}>{(currentQuestionIndex+1) >= getQuestionsService.length ? 'Submit':'Next'}</button>
            <h6 className={( interviewState === INTERVIEW_STATES.COMPLETE_INTERVIEW || interviewState === INTERVIEW_STATES.ASKING_QUESTION ) ? "hide":""}> Answer the question through your MIC .... <h2>{counter}</h2> </h6>
        </div>
        </section>
      );
  }
  
}
export default AttendInterviewPage;
