import { authActions } from "./auth";
import { useHistory } from "react-router-dom";

//? APIKit to use Axios
import APIKit, { setClientToken } from "Vintro/API/APIKit";

export const loginInWithCredentials = (loginEmail, loginPassword) => {
  return async (dispatch) => {
    const onSuccess = ({ data }) => {
      console.log({ data });
      setClientToken(data.access);
      dispatch(
        authActions.Load({
          token: data.access,
          data: data,
        })
      );
      localStorage.setItem("userData", JSON.stringify(data));
      const history = useHistory();
      history.replace("/admin/invites");
    };

    const onFailure = (error) => {
      if (error.response)
        dispatch(authActions.Error({ error: error.response.data.detail }));
    };

    dispatch(authActions.Request());

    const payload = { email: loginEmail, password: loginPassword };
    APIKit.post("/login/", payload).then(onSuccess).catch(onFailure);
  };
};

export const logout = () => {
  return async (dispatch) => {
    console.log("Logout");
    //authCtx.logout();
    dispatch(authActions.logout());
    localStorage.removeItem("userData");
  };
};
